@font-face {
  font-family: "Styrene A Web";
  src:
    url("../fonts/StyreneAWeb-Regular.woff2") format("woff2"),
    url("../fonts/StyreneAWeb-Regular.woff2") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Styrene A Web";
  src:
    url("../fonts/StyreneAWeb-Bold.woff2") format("woff2"),
    url("../fonts/StyreneAWeb-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

